import { useContext, useState } from "react";
import { DataContext } from "../../contexts/dataContext";

const InfoForm = (props) => {
    const { runFetch, setRunFetch } =useContext(DataContext);
    const [updateUser, setUpdateUser] = useState(props.thisUser);

    const changeInput = e => {
        // setThisUser({...thisUser, [e.target.name]: e.target.value})
        setUpdateUser({...updateUser, [e.target.name]: e.target.value})
    }

    const onSubmit = e => {
        e.preventDefault();

        console.log({...updateUser, action: "Update"})

        fetch(props.transactionUsersUrl, {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({...updateUser, action: "Update"})
        })
        .then(res=>res.json())
        .then(data=>{
            console.log(data)
            if (data.result.toLowerCase().indexOf("ok")!==-1 || data.result.toLowerCase().indexOf("success")!==-1)  {
                props.setAlert(data.result)
                props.setIsUpdateBtn(false);
                setRunFetch(!runFetch)
            }
        })
        .catch(err=>console.error(err))

    }

    return(
        <form onSubmit={onSubmit}>
                        <section className="personal-info-inputs">
                            <div className="input-div">
                                <label>
                                    <p>user id<span>*</span></p>
                                    <input name="userid" type="text" placeholder="" value={updateUser.userid} onChange={changeInput} disabled />
                                </label>
                                <label>
                                    <p>password<span>*</span></p>
                                    <input name="password" type="password" placeholder="" value={updateUser.password} onChange={changeInput} required />
                                </label>
                                <label>
                                    <p>company</p>
                                    <input name="company" type="text" placeholder="" value={updateUser.company} onChange={changeInput} />
                                </label>
                                <label>
                                    <p>user name</p>
                                    <input name="username" type="text" placeholder="" value={updateUser.username} onChange={changeInput} />
                                </label>
                                <label>
                                    <p>title</p>
                                    <input name="title" type="text" placeholder="" value={updateUser.title} onChange={changeInput} />
                                </label>
                                <label>
                                    <p>email<span>*</span></p>
                                    <input name="email" type="email" placeholder="" value={updateUser.email} onChange={changeInput} required />
                                </label>
                                <label>
                                    <p>birthday</p>
                                    <input name="birthday" type="date" placeholder="" value={updateUser.birthday} onChange={changeInput} />
                                </label>
                                <label>
                                    <p>gender</p>
                                    <select name="gender"  value={updateUser.gender} onChange={changeInput} >
                                        <option value=""></option>
                                        <option value="m">male</option>
                                        <option value="f">female</option>
                                    </select>                        
                                </label>
                                <label>
                                    <p>telephone</p>
                                    <input name="telephone" type="tel" placeholder="" value={updateUser.telephone} onChange={changeInput} />
                                </label>
                                <label>
                                    <p>mobile</p>
                                    <input name="mobile" type="tel" placeholder="" value={updateUser.mobile} onChange={changeInput} /> 
                                </label>
                            </div>
                                <div className="address-div">
                                    <label><p>zipcode</p>
                                        <input name="zipcode" type="text" placeholder="" value={updateUser.zipcode} onChange={changeInput} />
                                    </label>
                                    <label><p>address</p>
                                        <input name="address" type="text" placeholder="" value={updateUser.address} onChange={changeInput} /> 
                                    </label>              
                                </div>    
                            </section>
                            <input type="submit" value="update"/>
                            <p>{props.alert}</p>
                        </form>
    )
}

export default InfoForm;