/* eslint-disable react-hooks/exhaustive-deps */
import React, { useContext, useEffect, useState } from "react";
import { DataContext } from "../../contexts/dataContext";
import { InitContext } from "../../contexts/initContext";

import "../../styles/MyAccount.scss"
import PersaonalInfoInputs from "../others/PersonalInfoInputs";
import InfoForm from "./InfoForm";

function MyAccount (props) {
    const { thisUser, setThisUser } = useContext(DataContext);
    const { transactionUsersUrl } = useContext(InitContext);
    const [isUpdateBtn, setIsUpdateBtn] = useState(false);
    const [alert, setAlert] = useState("")

    const clickUpdateBtn = e => setIsUpdateBtn(!isUpdateBtn)

    const clickDelBtn = e => {
        fetch(PersaonalInfoInputs, {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                "action" : "Delete ",
                "userid1": thisUser.userid, 
            })
        })
        .then(res=> res.json())
        .then(data=>{
            console.log(data)
        })
        .catch(err => console.error(err))
    }

    return(
        <>
            <main id="MyAccount">
                <h1>my account</h1>
                <section className="personal-info-div">
                    <h2>personal infomation</h2>
                    <div>
                        <button onClick={clickUpdateBtn}>update</button>   
                        <button onClick={clickDelBtn}>delete</button>
                        <p>{alert}</p>
                    </div>
                    <ul>
                        <li>user id: <span>{thisUser.userid}</span></li>
                        <li>user name: <span>{thisUser.username}</span></li>
                        <li>email: <span>{thisUser.email}</span></li>
                        <li>company: <span>{thisUser.company}</span></li>
                        <li>title: <span>{thisUser.title}</span></li>
                        <li>telephone: <span>{thisUser.telephone}</span></li>
                        <li>zipcode: <span>{thisUser.zipcode}</span></li>
                        <li>address: <span>{thisUser.address}</span></li>
                    </ul>
                    { isUpdateBtn && <InfoForm thisUser={thisUser} setThisUser={setThisUser} transactionUsersUrl={transactionUsersUrl} alert={alert} setAlert={setAlert} setIsUpdateBtn={setIsUpdateBtn}/>}
                </section>
                <section>
                    <h2>account details</h2>
                        <ul>
                            <li>membership: <span>{thisUser.usertype}</span></li>
                            <li>membership account status: <span>{thisUser.enabled}</span></li>
                            <li>uname: <span>{thisUser.name}</span></li>                          
                        </ul>
                </section>
            </main>            
        </>

    )
}

export default MyAccount;